  padding: 0;
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: repeat(4, auto);
  row-gap: 6px;
  column-gap: 28px;
}
.footer__list--info {
  padding-top: 3px;
}
@media (max-width: 767px) {
  .footer__list--info {
    padding-top: 0;
  }
}
.footer__list--navigation .footer__item {
  max-width: 220px;
}
.footer__list--info .footer__item {
  max-width: 336px;
}
@media (max-width: 1023px) {
  .footer__list--info .footer__item {
    max-width: 320px;
  }
}
@media (max-width: 1023px) {
  .footer__list {
    grid-auto-flow: row;
    grid-template-rows: none;
  }
}

.is-active .footer__list {
  margin-bottom: 14px;
}
.is-active .footer__title-button::after {
  content: none;
}

.footer__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
}
@media (max-width: 767px) {
  .footer__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 23px;
  }
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.8;
    text-decoration: underline;
  }
}
.footer__link.focus-visible:focus {
  opacity: 0.8;
  text-decoration: underline;
}

